.slider {
  color: #eee;
  background: #010101;
  height: 100vh;
  position: relative;
}

.slider .list .item {
  opacity: 0;
  transition: all .5s;
  position: absolute;
  inset: 0;
  overflow: hidden;
}

.slider .list .item img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.slider .list .item:after {
  content: "";
  background-image: linear-gradient(to top, #000000a7 40%, #0000, #000000c6);
  width: 100%;
  height: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
}

.slider .list .item .content {
  z-index: 1;
  width: 500px;
  max-width: 80%;
  position: absolute;
  top: 20%;
  left: 10%;
}

.slider .list .item .content p:first-child {
  text-transform: uppercase;
  letter-spacing: 10px;
}

.slider .list .item .content h2 {
  margin: 0;
  font-size: 100px;
}

.slider .list .item.active {
  opacity: 1;
  z-index: 10;
}

@keyframes showContent {
  to {
    filter: blur();
    opacity: 1;
    transform: translateY(0);
  }
}

.slider .list .item.active p:first-child, .slider .list .item.active h2, .slider .list .item.active p:nth-child(3) {
  filter: blur(20px);
  opacity: 0;
  animation: .5s ease-in-out .7s forwards showContent;
  transform: translateY(30px);
}

.slider .list .item.active h2 {
  animation-delay: 1s;
}

.slider .list .item.active p:nth-child(3) {
  animation-duration: 1.3s;
}

.arrows {
  z-index: 100;
  animation: 1s forwards arrow-open;
  position: absolute;
  top: 30%;
  right: 50px;
}

.arrows button {
  color: #eee;
  background-color: #eee5;
  border: none;
  border-radius: 5px;
  width: 40px;
  height: 40px;
  font-family: monospace;
  font-size: x-large;
  transition: all .5s;
}

.arrows button:hover {
  color: #000;
  background-color: #eee;
}

.thumbnail {
  z-index: 11;
  box-sizing: border-box;
  justify-content: center;
  gap: 10px;
  width: 100%;
  height: 250px;
  padding: 0 50px;
  animation: .8s .4s forwards thumbnail-open;
  display: flex;
  position: absolute;
  bottom: 10px;
  overflow: auto;
  transform: translateY(500px);
}

.thumbnail::-webkit-scrollbar {
  width: 0;
}

.thumbnail .item {
  filter: brightness(.5);
  flex-shrink: 0;
  width: 130px;
  height: 200px;
  transition: all .5s;
}

.thumbnail .item img {
  object-fit: cover;
  border-radius: 10px;
  width: 100%;
  height: 100%;
}

.thumbnail .item.active {
  filter: brightness();
}

.thumbnail .item .content {
  position: absolute;
  inset: auto 10px 10px;
}

@media screen and (width <= 678px) {
  .thumbnail {
    justify-content: start;
  }

  .slider .list .item .content h2 {
    font-size: 60px;
  }

  .arrows {
    top: 10%;
  }
}

@media screen and (height <= 740px) {
  .slider .list .item .content {
    top: 16%;
  }

  .slider .list .item .content p:first-child {
    letter-spacing: 8px;
    font-size: .9rem;
  }

  .slider .list .item .content h2 {
    font-size: 50px;
  }
}
